<template>
    <section id="chapters" class="hero is-primary is-bold is-medium">
        <h2 class="title is-size-2-desktop is-spaced">Defining Chapters</h2>
        <div class="hero-body">
            <div class="container">
                <div class="chapter-wrapper" v-for="(chapter, index) in chapters" v-bind:item="chapter" v-bind:index="index" v-bind:key="chapter._id">
                    <!-- <ChapterComponent :chapter="chapter"/> -->
                    <p>{{ chapter.text }}</p>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import ChapterService from '../services/ChapterService';

export default {
  name: 'ChapterComponent',
  data() {
    return {
      chapters: [],
      error: '',
      text: '',
    }
  },
  async created() {
    try {
      this.chapters = await ChapterService.getPosts();
    } catch(err) {
      this.error = err.message;
    }
  }
}
</script>

